import { format, parseISO } from "date-fns";
import { Link, graphql } from "gatsby";
import React from "react";
// import { Helmet } from "react-helmet"
import GlobalStyle, { BlogLayout, PostItem, ColumnLayout } from "../../styles";

function renderPost(data, key) {
  const { node: post } = data;
  return (
    <PostItem key={key}>
        <Link to={`/${post.slug}`}>{post.frontmatter.title}</Link>
        <p className="date">
          <time>{format(parseISO(post.frontmatter.date), 'MMMM d, yyyy')}</time>
        </p>
    </PostItem>
  );
}

function renderPosts(posts) {
  return posts
    .filter(post => post.node.frontmatter.title.length > 0)
    .map(renderPost);
}

export default function BlogPostsPage({ data }) {
  const { edges: posts } = data.allMdx;
  return (
    <React.Fragment>
      <GlobalStyle />
      <BlogLayout>
        <ColumnLayout>
          {renderPosts(posts)}
        </ColumnLayout>
      </BlogLayout>
    </React.Fragment>
  )
}

export const pageQuery = graphql`
  query {
    allMdx {
      edges {
        node {
          id
          excerpt
          slug
          frontmatter {
            title
            path
            date
            tags
          }
        }
      }
    }
  }
`;
